body {
  overflow-y: scroll;
}

.card {
  width: 300px;
  height: 380px;
  border: 2px solid white;
  background-image: repeating-linear-gradient(#77c1e8, aliceblue);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 50px 0;
  border-radius: 10px;
  margin: 10px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px solid black;
}
.name_job {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.name {
  font-size: 17px;
  font-weight: 900;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #00283d;
  line-height: 20px;
}
.job {
  color: #003c5b;
}
.location_div {
  display: flex;
  margin: 50px 0 10px 0;
  width: 40%;
  justify-content: flex-start;
  align-items: center;
  font-weight: 600;
  width: 70%;
}

.department_div {
  display: flex;
  margin: 10px 0;
  width: 70%;
  justify-content: start;
  align-items: center;
  font-weight: 600;
}

.location,
.department {
  margin-left: 20px;
}

.button {
  background-color: #003c5b;
  padding: 10px 20px;

  margin-top: 20px;
  border-radius: 5px;
  color: #0a320a;
  font-weight: bold;
  color: aliceblue;
  width: 80%;
  border: none;
  transition: 0.3s;
}

.button:hover {
  background-color: #0272aa;
  cursor: pointer;
}
.button:focus {
  transform: scale(1.1);
}
