.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #0d3c54;
}

.expense-item__description {
  display: flex;

  align-items: flex-end;

  justify-content: space-between;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
  transform: translateX(-18px);
}

.form_label {
  color: aliceblue;
  font-weight: 900;
  padding: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: translateX(-105px);
}
.comments {
  color: rgb(0, 28, 53);
  padding-bottom: 10px;
  font-size: 25px;
}
.comment_card {
  background-color: aliceblue;
}
.comment_div {
  padding: 10px 50px;
}
.merchant {
  transform: translateX(-60px);
}
@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
