@import url("https://fonts.googleapis.com/css2?family=Work+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Work Sans", sans-serif;
}

.Navbar {
  background: #006398;
  width: 100%;
  height: 10vh;
  display: flex;
  position: fixed;
  top: 0;
  z-index: 20;
  align-items: center;
  justify-content: space-around;
}
a {
  text-decoration: none;
}
.help {
  z-index: 20;

  color: white;
  font-weight: bold;
  text-decoration: none;
}

.help:hover {
  color: azure;

  text-decoration: underline;
}

.button_div {
  display: flex;
}

.button {
  padding: 10px 20px;
  margin-left: 30px;
  background-color: white;
  border: none;
  border-radius: 8px;
  border: 2px solid white;
  color: #004b73;
  font-weight: bold;
  transition: 0.3s;
}

.button:hover {
  cursor: pointer;
  background-color: transparent;
  color: white;
}

.title {
  color: aliceblue;
  font-size: 1.5rem;
  font-weight: bold;
}

@media screen and (max-width: 400px) {
  .title {
    margin-left: 15px;
    font-size: 1.2em;
  }
}
