.container {
  padding: 6% 10% 1% 10%;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  background-color: azure;
  width: 90%;
  height: 104%;
  box-shadow: 1px 1px 3px 2px rgb(197, 197, 197);
  border-radius: 10px;
  border: 3px solid rgb(31, 31, 134);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.file_input {
  display: none;
}

.label {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-color: #55a4ce;
  margin: 10px;
  transition: 0.3s;
}
.label:hover {
  cursor: pointer;
  transform: scale(1.1);
  background-color: #0a2432;
}

.upload_btn {
  padding: 10px 20px;
  border: 2px solid #2b8ec2;
  margin-top: 20px;
  border-radius: 5px;
  color: #0a2432;
  font-weight: bold;
  background: none;
  transition: 0.3s;
}

.upload_btn:hover {
  transform: scale(1.1);
  background-color: #0a2432;
  color: aliceblue;
  cursor: pointer;
}

.input_field {
  padding: 10px 10px;
  width: 25vw;
  border: 2px solid #015f8f;
  outline: none;
  background: transparent;
  border-radius: 5px;
  margin: 10px 0;
}

.label_h6 {
  margin-top: 20px;
}

.submit_btn {
  padding: 10px 20px;
  border: 2px solid #2b8ec2;
  margin-top: 20px;
  border-radius: 5px;
  color: #0a320a;
  font-weight: bold;
  background: none;
  transition: 0.3s;
}

.submit_btn:hover {
  transform: scale(1.1);
  background-color: #1d325e;
  color: aliceblue;
  cursor: pointer;
}

input:focus {
  background-color: #9adbfe;
  color: #1d325e;
  font-weight: 900;
}

img {
  height: 100px;
  width: 100px;
}
