.container {
  padding: 10% 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.action {
  background-color: transparent;
  border: 3px solid rgb(5, 0, 56);
  border-radius: 8px;
  font-size: 1rem;
  margin-top: 40px;
  padding: 20px 25px;
  width: 30vw;
  font-weight: bold;
  transition: 0.3s;
  color: #002f48;
}

.action:hover {
  background-color: #004b73;
  color: white;
  cursor: pointer;
}
.buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
