@import url("https://fonts.googleapis.com/css2?family=Work+Sans&display=swap");

.home {
  width: 100vw;
  padding: 10% 11%;
  font-family: "Work Sans", sans-serif;
  display: flex;
  background-color: #aad2e7;
  height: 100vh;
}

.title {
  font-size: 5rem;
  font-family: "Work Sans", sans-serif;
  color: #01254f;
  padding-right: 20%;
}

.cursor {
  font-size: 3rem;
}

.typewriter {
  font-size: 2rem;
}

.cursorClassName {
  color: black;
  font-size: 2rem;
}

.login {
  background-color: transparent;
  border: 3px solid rgb(5, 0, 56);
  border-radius: 8px;
  font-size: 2rem;
  margin-top: 40px;
  padding: 10px 15px;
  font-weight: bold;
  transition: 0.3s;
  color: #002f48;
}

.login:hover {
  background-color: #004b73;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  body {
    overflow-x: hidden;
  }
  .home {
    width: 100vw;
    height: 100vh;
  }
  .title {
    margin-top: 10%;
    padding-right: 0;

    width: 70vw;
    font-size: 3rem;
  }
  .typewriter {
    padding-left: 5px;
    font-size: 25px;
  }
  .login {
    margin-left: 0%;
  }
}

@media screen and (max-width: 500px) {
  body {
    overflow-x: hidden;
  }
  .home {
    width: 100vw;
    height: 100vh;
  }
  .title {
    margin-top: 20%;
    padding-right: 0;

    width: 70vw;
    font-size: 3rem;
  }
  .typewriter {
    padding-left: 5px;
    font-size: 25px;
  }
  .login {
    margin-left: 0%;
    font-size: 100%;
  }
}
@media screen and (max-width: 400px) {
  .home {
    width: 150%;
    height: 150vh;
  }
  .title {
    margin-top: 40%;
    font-size: 5rem;
  }
  .typewriter {
    padding-left: 5px;
  }
  .login {
    margin-left: 5px;
  }
}
